let sdb = new autophp()
let urlnya='https://gis.officialapp.website'
import Skill from '@/components/portofolio/Skill.vue'
import Skill2 from '@/components/portofolio/Skill2.vue'
export default {
  components:{Skill,Skill2},
  data () {
    return {
      activity:[],
      service:[],

    }
  },
  layout: 'a-5',
  methods: {},
  mounted () {
     sdb.collection("app_portofolio_activity",false,urlnya).doc().select(`select * from app_portofolio_activity where iduser='${this.$store.state.id}'`).then(res => {
        this.activity=res;
        this.$forceUpdate();
    });
      sdb.collection("app_portofolio_service",false,urlnya).doc().select(`select * from app_portofolio_service where iduser='${this.$store.state.id}'`).then(res => {
        this.service=res;
        this.$forceUpdate();
    });
    
  },
}
</script>
