<template>
    <div>
      <div class="row">

      <div class="col-12">
        <p class="pl-2 pt-2 text-lg font-semibold underline">Keahlian / Skill</p>
        <br>
      </div>
      <div class="col-12">
        <div class="row" v-if="subskill.length>0">
          <div class="col-6" v-for="(item, index) in subskill" :key="index+'subskill'">
            <p
              style="font-size:10px;"
              class="p-2  text-xs font-bold animate__animated animate__flipInX "
            >{{item.title}}</p>
            <div
              style="width: 100%;"
              class="animate__animated animate__flipInX "
            >
              <button type="button" class="btn btn-sm btn-dark rounded-circle ml-1" 
                :style="`background:${warnasubskill};`"
                 v-for="(item, index) in Math.round((item.persen*10)/100)" :key="index+'circle'"
                style="height:18px;width:18px;" >
              </button>
              <button type="button" class="btn btn-sm rounded-circle ml-1"
              style="height:18px;width:18px;background:lightgrey;"
              v-for="(item, index) in 10-Math.round((item.persen*10)/100)" :key="index+'circle2'"
              >
              </button>
              <!-- <div class="rounded-l-lg pt-1 pr-2" :style="`width:${item.persen}%;background:${item.warna};`" style="text-align:right;font-size:12px;">{{item.persen}}%</div> -->
            </div>
          </div>
          <div class="col-12">
            <p class="font-semibold p-3 underline mt-2">Main Skill/Keahlian</p>
          </div>
          <div class="col-12 " v-for="(item, index) in skill" :key="index+'skill'">
            <p
              style="font-size:10px;"
              class="p-2  text-xs font-bold animate__animated animate__flipInX "
            >{{item.title}}</p>
            <button type="button" class="btn btn-sm btn-dark rounded-circle ml-1" 
                :style="`background:${warnaskill};`"
                 v-for="(item, index) in Math.round((item.persen*10)/100)" :key="index+'circle'"
                style="height:18px;width:18px;" >
              </button>
              <button type="button" class="btn btn-sm rounded-circle ml-1"
              style="height:18px;width:18px;background:lightgrey;"
              v-for="(item, index) in 10-Math.round((item.persen*10)/100)" :key="index+'circle2'"
              >
              </button>
          </div>
          <div class="col-12">
            <br>
            <br>
            <br>
          </div>
        </div>
      </div>
      </div>
    </div>
</template>
<script>
import autophp from '@/plugins/autophp'
let sdb = new autophp()
let urlnya='https://gis.officialapp.website'
export default{
    data(){
        return{
            skill:[],
            subskill:[],
            warnaskill:'lightblue',
            warnasubskill:'lightgreen'
        }
    },
    mounted () {
        sdb.collection("app_portofolio_skill",false,urlnya).doc().select(`select * from app_portofolio_skill  where iduser='${this.$store.state.id}'`).then(res => {
            this.skill=res;
            this.warnaskill=res[0].warna
            this.$forceUpdate();
        });
        
        sdb.collection("app_portofolio_subskill",false,urlnya).doc().select(`select * from app_portofolio_subskill  where iduser='${this.$store.state.id}'`).then(res => {
            this.subskill=res;
            this.warnasubskill=res[0].warna
            this.$forceUpdate();
        });
  },
}
</script>